
const cars = [
  { type: "Economy", price: 5, img: 'https://res.cloudinary.com/dr2ijoarv/image/upload/v1690688132/economy_h1yzmt.png', model: "Toyota  Aqua" },
  { type: "Van", price: 7, img: 'https://res.cloudinary.com/dr2ijoarv/image/upload/v1690688132/vun_knsi27.png', model: " Ford Grand C-Max" },
  { type: "Premium", price: 10, img: 'https://res.cloudinary.com/dr2ijoarv/image/upload/v1690688132/premium_cxbme5.png', model: "Nissan Teana" },
  { type: "Pickup", price: 12, img: 'https://res.cloudinary.com/dr2ijoarv/image/upload/v1690688132/pickup_uqqv2y.png', model: "Tacoma Access " },
  { type: "SUV", price: 10, img: 'https://res.cloudinary.com/dr2ijoarv/image/upload/v1690688132/suv_sdymcj.png', model: "Hundai Tucson" },
];

export default function Cars({ setSelectedCar, selectedCar }) {
  return (
    <div className="cars">
      {cars.map((el, i) => {
        return (
          <div
            className={selectedCar?.type === el.type ? "car car-active" : "car"}
            key={i}
            onClick={() => setSelectedCar(cars[i])}
          >
            <div className="img-box">
              {" "}
              <img className="car-img" src={el.img} alt={el.type} />
            </div>

            <p className="car-type">{el.type}</p>
            <p className="car-rent-price">
              {`${el.price}+$ `} <br />
              <span className="tariff">per hour</span>
            </p>
          </div>
        );
      })}
    </div>
  );
}
